@import "./src/assets/sass/prepends.scss";

article {
  min-height: calc(100vh - 3rem - (1rem + 0.75rem * 1.25));
  background-image: linear-gradient(
      rgba(56, 100, 113, 0.8),
      rgba(56, 100, 113, 0.8)
    ),
    url("@/assets/img/LP-BG.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  text-shadow: 0 0 0.25rem #000;

  .logo-wraper {
    display: block;
    max-width: 400px;
    margin: 1rem auto;
  }

  p {
    color: $text-white;
    text-align: center;
    > * {
      display: inline-block;
    }
  }

  div#top-info {
    color: $text-white;
    margin-top: 3rem;
    .pure-menu-heading {
      border-color: $text-white !important;
    }
    .announcement-content {
      padding: 0.5em 1em;
      line-height: 1.5;
      time {
        padding: 0 0.5rem;
      }
    }
  }
}
